import { FunctionComponent } from "react";
import styles from "./AttractionsIcon.module.css";

type AttractionsIconType = {
  attractionsIcon?: string;
};

const AttractionsIcon: FunctionComponent<AttractionsIconType> = ({
  attractionsIcon,
}) => {
  return (
    <img className={styles.attractionsIcon} alt="" src={attractionsIcon} />
  );
};

export default AttractionsIcon;
