import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";
import styles from "./MoviesIcon.module.css";

type MoviesIconType = {
  moviesIcon?: string;

  /** Style props */
  moviesIconWidth?: Property.Width;
};

const MoviesIcon: FunctionComponent<MoviesIconType> = ({
  moviesIcon,
  moviesIconWidth,
}) => {
  const moviesIconStyle: CSS.Properties = useMemo(() => {
    return {
      width: moviesIconWidth,
    };
  }, [moviesIconWidth]);

  return (
    <img
      className={styles.moviesIcon}
      alt=""
      src={moviesIcon}
      style={moviesIconStyle}
    />
  );
};

export default MoviesIcon;
